h1 {
    margin-bottom: 2rem;
}
h2 {
    margin: 0.5rem 0;
}
.price {
    color: #c83b0e;
    font-weight: bold;
}
.category {
    opacity: 0.5;
}

.productList {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: space-between;
    align-items: start;
}

.productItem {
    flex: 0 0 calc(33.33% - 1rem);
    text-align: start;
}

.productPicture {
    position: relative;
    width: 100%;
    max-width: 100%;
    img:not(.icon) {
        width: 100%;
        max-width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 0.5rem;
    }
    &::after {
        content: '';
        display: table;
        clear: both;
    }
}

.icon {
    margin-right: 1rem;
}
.quantity,
.addText {
    font-size: 0.8rem;
}

.addBtn {
    background-color: #fff;
    border-radius: 3rem;
    width: 50%;
    min-width: 8rem;
    position: absolute;
    bottom: -14px;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
}

.addBtnInner {
    padding: 0.5rem 0.5rem 0.5rem 0.5rem;
    border-radius: 3rem;
    display: flex;
    align-items: center;
    p {
        font-size: 1rem;
    }
}
.addBtnInner1 {
    justify-content: center;
    border: 1px solid #aaa;
    &:hover {
        border: 1px solid #c83b0e;
        .add-text {
            color: #c83b0e;
        }
    }
}
.addBtnInner2 {
    justify-content: space-between;
    background-color: #c83b0e;
    color: #fff;
    border: 1px solid #c83b0e;
}
.decreaseBtn,
.increaseBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.5rem;
    height: 1.5rem;
    i {
        font-size: 1rem;
    }
    border: 1px solid #fff;
    border-radius: 50%;
    &:hover {
        background-color: #fff;
        i {
            color: #c83b0e;
        }
    }
}

.category {
    margin-top: 2rem;
}

@media (max-width: 768px) {
    .productItem {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

@media (max-width: 1440px) {
    .productItem {
        flex: 0 0 calc(50% - 1rem);
    }
}
